import { mapState, mapActions, mapGetters, mapMutations } from 'vuex'

export default {
    data() {
        return {
            structureTitles: ['  1- :', '  2- :',
            '  3- :', '  :', ' :', '  :'],
            activityTitles: [' - ', '  ', '-   ',
            '-   ', '-   . ', '   ']
        }
    },
    computed: {
        ...mapGetters(['Referals'])
    },
    props: {
        
    },
    methods: {
        ...mapActions(['fetchStatusSession', 'fetchDataReferals']),
    },
    components: {
        
    },
    created(){
        this.fetchStatusSession().then(data => {
            this.fetchDataReferals();
        })
    }
}


